}

.loader-container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 100px;
  height: 30px;
  background: #0000004d;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 35px 0 -5px #aaa, 0 -5px #ddd, 0 -25px 0 -5px #fff, -25px -30px #ddd, -25px 30px #ddd, 25px -30px #ddd, 25px 30px #ddd, 20px 10px 0 5px #ddd, 20px -10px 0 5px #ddd, -20px -10px 0 5px #ddd, -20px 10px 0 5px #ddd;
}

.loader:after, .loader:before {
  content: "";
  width: 35px;
  height: 35px;
  box-sizing: border-box;
  border: 4px dashed #fff;
  border-radius: 100%;
  animation: 4s linear infinite tape;
  display: block;
  position: absolute;
  bottom: -4px;
  transform: rotate(0);
}

.loader:before {
  right: 0;
  box-shadow: 0 0 0 4px #fff, 0 0 0 34px #000;
}

.loader:after {
  left: 0;
  box-shadow: 0 0 0 4px #fff, 0 0 0 65px #000;
}

@keyframes tape {
  0% {
    transform: rotate(0)scale(.4);
  }

  100% {
    transform: rotate(-360deg)scale(.4);
  }
}

